<template>
  <div id="app">
    <transition :name="animate">
      <router-view class="Router"></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      animate: ""
    };
  },
  //使用watch 监听$router的变化
  //如果to索引大于from索引,判断为前进状态,反之则为后退状态
  watch: {
    $route(to, from) {
      if (!from.meta) return;
      document.title = to.meta && to.meta.title || "建信人寿";
      if (to.meta.level > from.meta.level) {
        //设置动画名称
        this.animate = "slide-left";
      } else {
        this.animate = "slide-right";
      }
      console.log(this.animate);
    },
    route(val) {
      console.log(val);
      this.$router.replace({
        pathName:val.name
      })
      
    }
  }
};
</script>

<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#nav {
  padding: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}

.Router {
  position: absolute;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 500ms;
  position: absolute;
}

.slide-right-enter {
  opacity: 0;
  // transform: translate3d(-100%, 0, 0);
  transform: scale(0.1);
}

.slide-right-leave-active {
  opacity: 0;
  // transform: translate3d(100%, 0, 0);
  transform: scale(1.5);
}

.slide-left-enter {
  opacity: 0;
  // transform: translate3d(100%, 0, 0);
  transform: scale(0.1);
}
.slide-left-leave-active {
  opacity: 0;
  // transform: translate3d(-100%, 0, 0);
  transform: scale(1.5);
}
</style>
