<template>
    <van-overlay :show="showDialog" class="dialog-overlay">
        <div class="card-dialog">
            <div class="title">
                <img :src="title.img" alt="" v-if="title.img">
                <br>
                {{title.text || "提示"}}
            </div>
            <div class="content">
                <slot></slot>
                <div>{{content}}</div>
                <br>
                <div>
                    {{rowText}}
                </div>
            </div>
            <div class="button" @click="confirm">{{buttonText}}</div>
        </div>
    </van-overlay>
</template>

<script>
export default {
    name:"card-dialog",
    data() {
        return {
            showDialog:false,
            rowText:'',
            buttonText:"我知道了",
            title:{
                img:require("@/assets/image/tip.png"),
                text:""
            },
            content:"",
            callbak:null
        }
    },
    methods:{
        show(payLoad) {
            const {buttonText,rowText,title,content,callbak} = payLoad;
            this.buttonText = buttonText || this.buttonText;
            this.title = title || this.title;
            this.content = content || this.content;
            this.callbak = callbak;
            this.rowText = rowText || ''
            this.$nextTick().then(() => this.showDialog = true)
        },
        confirm() {
            console.log("callbak:",this.callbak);
            this.showDialog = false;
            this.callbak && this.callbak();
        }
    }
}
</script>

<style lang="less" scoped>
.dialog-overlay {
    z-index: 99999;
}
.card-dialog {
    width: 72vw;
    text-align: center;
    position:fixed;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
    background-color:#fff;
    padding: 15px 0 0;
    border-radius: 4px;
    .title {
        font-size: 4.2vw;
        font-weight: 500;
        color:#303133;
        img {
            width: 10vw;
        }
    }
    .content {
        font-size: 3.7vw;
        padding: 17px
    }
    .button {
        height: 50px;
        line-height: 50px;
        border-top:1px solid #ddd;
        color: #108ee9;
    }
}
</style>
