import LoadingComponent from "./index.vue";
import Vue from "vue";

let $vm
let delayTime = null
const Skeleton = Vue.extend(LoadingComponent)
if (!$vm) {
    $vm = new Skeleton({
        el: document.createElement('div')
    })
    document.body.appendChild($vm.$el)
}
const skeleton = {
    show(options = {}) {
        delayTime && clearTimeout(delayTime)
        delayTime = setTimeout(() => {
            $vm.show = true
        }, options.delay || 0)
    },
    hide() {
        if (delayTime) {
            clearTimeout(delayTime)
            delayTime = null
        }
        $vm.show = false
    },
    install(Vue) {
        if (this.installed) {
            return
        }
        this.installed = true
        Object.defineProperties(Vue.prototype, {
            $skeleton: {
                get: function get() {
                    return this
                }
            }
        })
    }
}


export default skeleton;
