import DialogComponent from "./index.vue";
import Vue from "vue";

let $vm
let delayTime = null
const Dialog = Vue.extend(DialogComponent)
if (!$vm) {
    $vm = new Dialog({
        el: document.createElement('div')
    })
    document.body.appendChild($vm.$el)
}
const dialog = {
    show(options = {}) {
        delayTime && clearTimeout(delayTime)
        delayTime = setTimeout(() => {
            $vm.show(options);
        }, options.delay || 0)
    },
    install(Vue) {
        if (this.installed) {
            return
        }
        this.installed = true
        Object.defineProperties(Vue.prototype, {
            $dialog: {
                get: function get() {
                    return this
                }
            }
        })
    }
}


export default dialog;
