import Vue from "vue";
import { axios } from "./utils/request";
import router from "./router";
import store from "./store";
import skeleton from "@/components/skeleton";
// const appid = location.origin.indexOf("yuqi56") > -1 ? "wx35160965183cd183":"wx595c978b7b5512cb"; 
//测试appid：wxf77bf8625430e3f8 投产测试appid：wx35160965183cd183 生产：wx595c978b7b5512cb
const isWechat = /MicroMessenger/i.test(navigator.userAgent.toLowerCase());
let openid = localStorage.getItem("elife-openid");
const search = location.search.replace("?", "");
let isReferrerCode = /referrerCode/g
let isYxpt = isReferrerCode.test(location.href)
let CCB = /SaleChnl/.test(location.href)
let isHly = /HLY/.test(location.href) //惠联运
let isJrhx = /JRHX/.test(location.href) //建融惠学
let isJrjy = /channel=0035/.test(location.href)
let ccbParamSJ = /ccbParamSJ/.test(location.href)
let ynt = /custType/.test(location.href)
console.log(location.href, 'location.href')
let appid = 'wx595c978b7b5512cb'
if (location.origin.indexOf("bugbank") > -1) {
  let env = location.pathname.replace('/elife-card/', '')
  appid = env.indexOf('nvt') > -1 ? 'wx35160965183cd183' : env.indexOf('nsit01') > -1 ? 'wxbee5fc5fd24cdc2a' : env.indexOf('nsit02') > -1 ? 'wx35160965183cd183' : env.indexOf('nsit03') > -1 ? 'wxe2abce25a83311ea' :  env.indexOf('nsit04') > -1 ? 'wx35160965183cd183' : ''
}
if ((isWechat || isYxpt) && !openid) {
  if (search.indexOf("code") < 0) {
    window.location.replace(`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${encodeURIComponent(location.href)}&response_type=code&scope=snsapi_base&state=test#wechat_redirect`)
    // window.location.replace('https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxe2abce25a83311ea&redirect_uri=' + encodeURIComponent(location.href) + '&response_type=code&scope=snsapi_base&state=test#wechat_redirect')
  } else {
    const code = search.split("&")[0].split("=")[1];
    store.dispatch("getOpenId", code, isYxpt)
  }
} else if (openid) {
  store.dispatch("getUserInfo", openid)
}

let channelCode = "0015"
let channelCodeOld = "812"
if (sessionStorage.getItem("channelCode")) channelCode = sessionStorage.getItem("channelCode")
if (isWechat) {
  channelCode = "0011",
    channelCodeOld = "811"
}

if (isYxpt) {
  channelCodeOld = "827",
    channelCode = "9999"
  // 营销平台
}

if (CCB) {
  channelCodeOld = "835",
    channelCode = "0035"
}

if (isJrjy) {
  channelCodeOld = "835",
    channelCode = "0035"
}

if (isHly) {
  channelCode = "0024"
}
if (ynt) {
  channelCode = '0021'
  // 官微裕农通
}
if (navigator.userAgent.indexOf('CloudMercWebView') > -1 || ccbParamSJ) {
  channelCode = '0015'
  channelCodeOld = '812'
  console.log('建行生活')
}
if (channelCode == '0032') channelCodeOld = '0032' // 惠懂你渠道
if (channelCode == '0021') channelCodeOld = '0021'
// "0032" "803"
if (location.href.indexOf('channel=') != -1) {
  channelCode = location.href.split('channel=')[1];
}

sessionStorage.setItem("channelCode", channelCode);
store.commit("SET_CHANNEL_CODE", channelCode)

sessionStorage.setItem("channelCodeOld", channelCodeOld);
store.commit("SET_CHANNEL_CODE_OLD", channelCodeOld)

// eslint disabled nextline
let num = 0
router.beforeEach((to, from, next) => {
  let routerQuery = { ...JSON.parse(JSON.stringify(to.query)) }
  console.log(routerQuery)
  if (routerQuery.referrerCode) {
    routerQuery.referrerName = decodeURIComponent(routerQuery.referrerName)
    sessionStorage.setItem("referrer", JSON.stringify(routerQuery))
    if (localStorage.getItem("elife-openid") && num == 0) {
      num++
      store.dispatch("saveRecord")
    }
  }
  store.commit("SET_ROUTER_QUERY", routerQuery)
  if (to.name === "card-activity-collection") {
    const BASE_URL = process.env.VUE_APP_API_BASE_URL || '';
    axios.get(`${BASE_URL}/statics/json/card/card-collection-page-json-${to.params.id || ''}.json`).then(res => {
      store.commit("WRITE_IN_COLLECTION", res);
      Vue.prototype.$loading.hide();
      console.log(res);

      next();
    })
    return;
  };
  if (to.name === "card-activity-transit") {

    const BASE_URL = process.env.VUE_APP_API_BASE_URL || '';
    console.log(BASE_URL);

    axios.get(`${BASE_URL}/statics/json/card/card-collection-page-json-${to.params.id || ''}.json`).then(res => {
      store.commit("WRITE_IN_COLLECTION", res);
      Vue.prototype.$loading.hide();

      // skeleton.show()
      next();
    })
    return;
  }
  if (to.name === "whiteList") {
    sessionStorage.setItem("XPX", 200);
    const BASE_URL = process.env.VUE_APP_API_BASE_URL || '';
    console.log(to);
    axios.get(`${BASE_URL}/statics/json/card/card-collection-page-json-${to.params.id || ''}.json`).then(res => {
      console.log(res);
      // store.commit("WRITE_IN_COLLECTION", res);
      store.commit("student_json", res);


      Vue.prototype.$loading.hide();
      // skeleton.show()
      next();
    })
    return;
  }
  if (to.name === "card-info") {
    if (store.state.app.checkPass) return next();
    if (store.state.app.batchInfo.plchdRespCode === "1") {
      return next();
    }
    return store.dispatch("activationApply", to.params);
  };
  if (to.name === 'insuranceInformation') {
    if (store.state.app.checkPass) return next();
    if (store.state.app.batchInfo.plchdRespCode === "1") {
      return next();
    }
    return store.dispatch("activationApply", to.params);
  }
  if (to.name === "card-result") {
    console.log("card-resultresultresult1", sessionStorage.getItem('yntInfo'));

    if (to.params.type === "0" || to.params.type === "1" || to.params.type === "2") {
      sessionStorage.getItem('yntInfo') && store.dispatch("saveYntInfo", to.params);
      console.log("card-resultresultresult2");
      return next();
    }
    console.log("card-resultresultresult3");
    return store.dispatch("dispatchFindPolicyNo", to.params.orderCode);
  }
  console.log("card-resultresultresult4");
  next();
});
