const install = {
    vm: {},
    install(Vue, instance) {
        if (this.installed) {
            return
        }
        this.installed = true
        Object.defineProperties(Vue.prototype, {
            api: {
                get: function get() {
                    return instance
                }
            },
            $api: {
                get: function get() {
                    return instance
                }
            }
        })
    }
}

export {install};