import {api} from "@/api"
import skeleton from "@/components/skeleton";
import router from "@/router";
import loading from "@/components/loading/index.js"
import dialog from "@/components/dialog/index.js";
let isDialog = true

function showDialog(isShow, callbak) {
    if (isShow && isDialog) {
        dialog.show({
            title: {
                img: require("@/assets/image/tip.png"),
                text: "温馨提示"
            },
            rowText:isShow.tips,
            content: isShow.tip ? isShow.tip : "您即将进入投保流程，请您认真阅读保险条款。为保障您的权益，您的投保操作流程将被记录，请知悉！",
            buttonText: isShow.btn ? isShow.btn : "我知道了",
            callbak: function () {
                callbak && callbak()
            }
        })
    } else {
        callbak && callbak()
    }
}

const app = {
    state: {
        userCode: '',
        event: null,
        collection: {},  // 聚合页面配置
        policyInfo: {}, // 用户信息
        lifeInfo:{}, // 建行生活相关数据
        productData: {},  // 卡单产品信息
        materialList: [], // 条款
        batchInfo: {},// 批次申请信息
        flowNo: "",   //  激活查询流水号
        channelCode: "",
        batchNo:"",//批次号
        routeParams: {},
        batch: {},
        hasUserInfo: false,
        time: 0,
        minDate: new Date(+new Date + 86400000),
        maxDate: new Date(+new Date + 30 * 86400000),
        holdData: {},// 核保params
        result: {},// 支付成功后结果页 信息
        checkPass: false,
        hasSomeUserInfo: false,
        fromPageName: "",
        importActiveFlag: null,
        sendSalerId: '',
        sceneCode: '',
        schoolData: {},
        wxShareInfo: {},
        routeQuery: {},
        checkPhoneID: true,
        exonerationNoticeCardRisk: {}, // 免除责任和特别提示
        hasCheckHold: false,
        checkData:{},
        orderCode:'',
        imgCode:{},
        getType:'',
        // 学平险
        studentJosnLsit: {},


    },
    getters: {
        product(state) {
            return {...state.productData, ...state.batchInfo, ...state.routeParams}
        }
    },
    mutations: {
        // 学平险
        student_json: (state, option) => {
            state.studentJosnLsit = option
        },



        getCode: (state, data) => {
            state.userCode = data
        },
        SET_Exoneration_Notice: (state, option) => {
            state.exonerationNoticeCardRisk = option
        },
        WRITE_IN_COLLECTION: (state, option) => {
            state.collection = option
        },
        SET_PRODUCT_DATA: (state, option) => {
            state.productData = option
            localStorage.setItem("PRODUCTDATA",JSON.stringify(option))
        },
        SAVE_CARD_FLOWNO: (state, flowNo) => {
            state.flowNo = flowNo
        },
        SET_BATCH_INFO: (state, info) => {
            state.batchInfo = info;
        },
        SET_CHECK_DATA: (state, data) => {
            state.checkData = data;
        },
        SET_MATERIAL_LIST: (state, list) => {
            state.materialList = list
        },
        SET_USER_INFO: (state, info) => {
            const {customerName, cretNo, phoneNo, plchdEmailAdr, insureName, insureIdCard, choosedRela, date} = info.policyInfo
            state.policyInfo = {
                policyName: customerName,
                policyIdCard: cretNo,
                policyPhone: phoneNo,
                email: plchdEmailAdr,
                insureName,
                insureIdCard,
                choosedRela,
                date
            };
            state.hasUserInfo = !!info.hasUserInfo;
            state.hasSomeUserInfo = !!info.hasSomeUserInfo;
        },
        SET_LIFE_INFO: (state, info) => {
            // const {customerName, cretNo, phoneNo, plchdEmailAdr, insureName, insureIdCard, choosedRela, date} = info.policyInfo
            state.lifeInfo = info.lifeInfo
            const {MOBILE,Usr_Name} = info.lifeInfo
            state.policyInfo.policyPhone = MOBILE
            state.policyInfo.policyName = Usr_Name
            state.hasSomeUserInfo = !!info.hasSomeUserInfo;
        },
        SET_CHANNEL_CODE: (state, channelCode) => {
            state.channelCode = channelCode
        },
	SET_BATCHNO:(state,batchNo)=>{
            state.batchNo = batchNo
        },
        SET_CHANNEL_CODE_OLD: (state, channelCodeOld) => {
            state.channelCodeOld = channelCodeOld
        },
        SAVE_RESULT: (state, order) => {
            const holdResult = JSON.parse(localStorage.getItem("card-holdResult"));
            state.result = {order, ...holdResult}
        },
        SET_FROM_PAGE_NAME: (state, name) => {
            state.fromPageName = name
        },
        SET_DATE: (state, date) => {
            state.minDate = date.minDate;
            state.maxDate = date.maxDate || new Date(+new Date(date.minDate) + 30 * 86400000);
        },
        SET_IMPORT_FLAG: (state, flag) => {
            state.importActiveFlag = flag
        },
        SET_SEND_SALER_ID: (state, option) => {
            state.sendSalerId = option
        },
        SET_SCHOOL: (state, CCB) => {
            state.schoolData = CCB
        },
        SET_SCENE_CODE: (state, option) => {
            state.sceneCode = option
        },
        SET_IMG_CODE: (state, option) => {
            state.imgCode = option
        },
        GET_TYPE: (state, option) => {
            state.getType = option
        },
        SET_ROUTER_QUERY: (state, option) => {
            state.routeQuery = option
        },
        SER_CHECK_FLAG: (state, flag) => {
            state.checkPhoneID = flag;
        },
        SET_ORDERCODE:(state,option)=>{
            state.orderCode=option;
        },
         //继续支付设置信息
         CONTINUE_PAY_MES:(state,option)=>{
            //  console.log('8888888888888',option);
            // //  result页需要的
            // 跳到结果页面结果页面会有result
            state.channelCode=option.channelCode;
            state.orderCode=option.orderCode;
            state.batchNo=option.batchNo;
            localStorage.setItem('card-holdResult',option.cardHoldResult);
            // // blankpage页需要的
            state.holdData.orderCode=option.orderCode
            state.holdData.plchdNm=option.plchdNm;
            state.productData.prd_name=option.prdName;
            state.batchInfo.initSumAmt=option.initSumAmt  
          


        },
    },
    actions: {
        activationApply: async ({state, dispatch}, payLoad) => {
            skeleton.show();
            state.routeParams = payLoad
            const { batchNo, cardNo, cardPwd } = payLoad
            state.batch = { batchNo, cardNo, cardPwd }
            let param = {
                channelCode: state.channelCode
            }
            if (batchNo) {
                if (state.fromPageName !== "card-activity-collection") {
                    const collections = await api.card.collections({}, true)
                    const activityId = Object.keys(collections).find(v => {
                        if (collections[v].includes(batchNo)) {
                            return v
                        }
                    })
                    if (activityId) {
                        skeleton.hide();
                        if(navigator.userAgent.indexOf('CloudMercWebView') > -1){
                            skeleton.show();
                            console.log(isDialog,'应该弹窗的')
                            let batchFlagDate = {};
                            let batFlag = await api.static.batchFlag({}, true).then(resJson => {
                                loading.show();
                                for (let s = 0; s < resJson.batchFlagDate.length; s++) {
                                    if (batchNo == resJson.batchFlagDate[s].batch) {
                                        batchFlagDate = {
                                            tip: resJson.batchFlagDate[s].tip,
                                            tips:resJson.batchFlagDate[s].tips,
                                            btn:'投保'
                                        };
                                        return batchFlagDate
                                    }
                                }
                                
                            })
                            console.log(batFlag,'batFlag')
                            showDialog(batFlag,()=>{
                                skeleton.hide();
                                isDialog = false
                            })
                        }
                        return router.replace({
                            name: "card-activity-collection",
                            params: {
                                id: activityId
                            },
                            query: state.routeQuery
                        })
                    }
                }
            }
            if (cardNo && cardPwd) {
                param.plchdCardNo = cardNo;
                param.plchdCardPwd = cardPwd;
            } else if (batchNo) {
                param.batchNo = batchNo;
            }
            return api.card.activationApply(param, true).then(res => {
                if (res.status === "1") {
                    let insuranceInformation = {
                      plchdCardNo: res.data,
                    };
                    sessionStorage.setItem(
                      "insuranceInformation_PAGE",
                      JSON.stringify(insuranceInformation)
                    );
                    state.time = 0;
                    return dispatch("cardActiveResult", res.data)
                } else {
                    skeleton.hide();
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "0",
                            message: res.error.message
                        }
                    })
                }
            })
        },
        cardActiveResult: ({state, dispatch, commit}, flowNo) => {
            if (++state.time > 20) return;
            return api.card.cardActiveResult({ flowNo }, true).then(res => {
                console.log(res,'sadasdsadsadssa');
                if (res.status === "1") {
                    if (res.data) {
                        if(state.batch.batchNo != res.data.batchNo && /KDPC/.test(state.batch.batchNo)){
                            router.replace({
                                name: "card-info",
                                params: {
                                  batchNo: res.data.batchNo
                                },
                                query: state.routeQuery
                              });
                              return
                        }
                        commit("SET_IMPORT_FLAG", res.data.importActiveFlag)
                        localStorage.setItem('BATCHNO',JSON.stringify(res.data.batchNo))
                        let batchFlagDate = {};
                        api.static.batchFlag({}, true).then(resJson => {
                            loading.hide();
                            for (let s = 0; s < resJson.batchFlagDate.length; s++) {
                                if (res.data.batchNo == resJson.batchFlagDate[s].batch) {
                                    batchFlagDate = {tip: resJson.batchFlagDate[s].tip};
                                    break;
                                }
                            }
                            switch (res.data.plchdRespCode) {
                                case "0": // 激活失败
                                    skeleton.hide();
                                    router.replace({
                                        name: "card-result",
                                        params: {
                                            type: "0",
                                            message: res.data.plchdRespDesc
                                        }
                                    })
                                    break;
                                case "1": // 激活成功                                   
                                    commit("SET_BATCH_INFO", res.data);
                                    dispatch("getProductResult", {
                                        prdId: res.data.prdId,
                                        isShow: batchFlagDate.tip ? batchFlagDate : true
                                    });
                                    break;
                                case "2": // 白名单激活，跳往白名单页面0
                                    // 判断是否是学平险的
                                    if (sessionStorage.getItem('XPXWHITE')) {
                                        let a = {
                                            batchNo: JSON.parse(sessionStorage.getItem('planPage')).KDPC, //批次号
                                            plchdCrdtNo: JSON.parse(sessionStorage.getItem('whiteList_json')).IDcard.toUpperCase().replace(/\s+/g, ""), //证件号码
                                            channelCode: sessionStorage.getItem("channelCode")
                                        }
                                        dispatch('cardValidationCretNo', a)


                                        break;
                                    } else {
                                        let isShow = batchFlagDate.tip ? batchFlagDate : true
                                        showDialog(isShow, () => {
                                            skeleton.hide();
                                            commit("SER_CHECK_FLAG", false)
                                            router.replace({
                                                name: "info-check",
                                                params: state.routeParams
                                            })
                                        })
                                        break;
                                    }
                            }


                        })
                    } else {
                        setTimeout(() => {
                            dispatch("cardActiveResult", flowNo)
                        }, 3000)
                    }
                }
            })
        },
        getProductResult: async ({state, dispatch, commit, getters}, option) => {
            const {prdId, isShow} = option;
            if(sessionStorage.getItem('ccbUserParam')){
                let lifeInfo = JSON.parse(sessionStorage.getItem('ccbUserParam'))
                commit("SET_LIFE_INFO", {lifeInfo,hasSomeUserInfo: true})
            }
            if (state.routeQuery.userInfo) {
                await api.card.decryptUserInfo({
                    batchNo: getters.product.batchNo || '',
                    ccbMerchantId: getters.product.ccbMerchantId,
                    ccbPosId: getters.product.ccbPosId,
                    userInfo: decodeURIComponent(state.routeQuery.userInfo)
                }, true).then(res => {
                    const policyInfo = {
                        cretNo: res.IDTYPE == "01" ? res.ID : '',
                        phoneNo: res.PHONE,
                        customerName: res.NAME
                    };
                    commit("SET_USER_INFO", {policyInfo, hasSomeUserInfo: true})
                })
            } else if (state.routeQuery.SaleChnl || sessionStorage.getItem('userData')) {//建融家园获取用户信息
                if (sessionStorage.getItem('userData')) {
                    const userData = JSON.parse(sessionStorage.getItem("userData"));
                    const policyInfo = {
                        cretNo: userData.cretType == "A" || userData.cretType == "1010" ? userData.cretNo : '',
                        phoneNo: userData.phoneNo,
                        customerName: userData.customerName
                    };
                    commit("SET_USER_INFO", {policyInfo, hasSomeUserInfo: true})
                } else {
                await api.card.register({
                    code: state.routeQuery.SaleChnl || "ccbhome@b12d8e",
                    usercode: state.routeQuery.code
                }, true).then(res => {
                    if (res.status == '1') {
                        const policyInfo = {
                            cretNo: (res.data.cretType == "1010" || res.data.cretType == "A") ? res.data.cretNo : '',
                            phoneNo: res.data.phoneNo,
                            customerName: res.data.customerName
                        };
                        commit("SET_USER_INFO", {policyInfo, hasSomeUserInfo: true})
                    }
                })
               }
            }else if(state.routeQuery.cId && state.routeQuery.busIo && state.routeQuery.cipText){
                await api.card.cusInfoEncode({
                    cid: state.routeQuery.cId,
                    bus: state.routeQuery.busIo,
                    ciText: state.routeQuery.cipText
                },true).then(res => {
                    if (res.status == '1'){
                        var userData = JSON.parse(res.data);
                        const policyInfo = {
                            cretNo: userData.idEntity,
                            phoneNo: userData.Tel,
                            customerName: userData.recogNizee
                        };
                        commit("SET_USER_INFO", {policyInfo, hasSomeUserInfo: true})
                    }else {
                        router.replace({
                            name: "card-result",
                            params: {
                                type: "0",
                                message: "该链接访问失败，请联系工作人员重试。"
                            }
                        })
                    }
                })
            }else if(state.routeQuery.ccbParamSJ){//建行生活获取用户信息
               const ccbParamData = await api.card.encrypt({cipherText:state.routeQuery.ccbParamSJ,ccbPosId:getters.product.ccbPosId}).then(res => {return res})
                if (ccbParamData.status == '1'){
                    let lifeInfo = ccbParamData.data
                    commit("SET_LIFE_INFO", {lifeInfo,hasSomeUserInfo: true})
                }else{
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "0",
                            message: ccbParamData.error.message
                        }
                    })
                    skeleton.hide();
                    return
                }
            }else if(state.routeQuery.cid && state.routeQuery.bus && state.routeQuery.ciText){
                if(state.routeQuery.cid=='JHXBK' && state.routeQuery.bus=='XBK001'){
                    await api.card.cusInfoEncode({
                        cid: state.routeQuery.cid,
                        bus: state.routeQuery.bus,
                        ciText:encodeURIComponent(state.routeQuery.ciText)
                    },true).then(res => {
                        if (res.status == '1'){
                            const userMessage={
                                recogNizee:res.data.recogNizee,
                                trainClassid:res.data.trainClassid,
                                insuranceid:res.data.insuranceid,
                                ccbempid:res.data.ccbempid,
                                trainClassStaDate:res.data.trainClassStaDate,
                                trainClassEndDate:res.data.trainClassEndDate,
                                cvaildate:res.data.cvaildate,
                                cid:res.data.cid,
                                bus:res.data.bus,
                                trainClassName:res.data.trainClassName,
                                campusName:res.data.campusName
                            }; 
                            sessionStorage.setItem('userMessage',JSON.stringify(userMessage))
                            const policyInfo = {
                                cretNo: '',
                                phoneNo: '',
                                customerName: res.data.recogNizee,
                                date:res.data.cvaildate
                            };
                            commit("SET_USER_INFO", {policyInfo, hasSomeUserInfo: true})
                        }else {
                            router.replace({
                                name: "card-result",
                                params: {
                                    type: "0",
                                    message: "该链接访问失败，请联系工作人员重试。"
                                }
                            })
                        }
                    })
                }
            }
            // const exonerationNoticeCardRisk = new Promise( api.static.exonerationNoticeCardRisk({}, true))
            let [productData, materialList, exonerationNoticeCardRisk] = await Promise.all([
                api.static.product({url: `/${state.channelCodeOld}/${prdId}/${prdId}.json`, params: {prdId}}, true),
                api.static.product({url: `/${state.channelCodeOld}/${prdId}/${prdId}MaterialList.json`}, true),
                // api.static.product({ url: `/${state.channelCodeNew}/${prdId}/${prdId}.json`, params: { prdId } }, true),
                // api.static.product({ url: `/${state.channelCodeNew}/${prdId}/${prdId}MaterialList.json` }, true),
                api.static.exonerationNoticeCardRisk({}, true)
            ])
            console.log(materialList.data, 'api里面的阅读条款');
            const mater = JSON.stringify(materialList.data);
            sessionStorage.setItem("materialList", mater);
            if (sessionStorage.getItem("referrer") && prdId) {
                dispatch("saveRecord", prdId)
            }
            const risk = productData.data.plans.map(v => v.planCode.slice(0, -2));
            const encr = exonerationNoticeCardRisk.filter(v =>risk.includes(v.risk)).sort((a, b) => risk.indexOf(a.risk) - risk.indexOf(b.risk));
            console.log(encr);

            let exoneration = {type: "exoneration", materialName: "责任免除", content: [], notPdf: true},
                hottip = {type: "hottip", materialName: "特别提示", content: [], notPdf: true};
            encr.forEach(v => {
                exoneration.content = exoneration.content.concat(v.exoneration);
                hottip.content = hottip.content.concat(v.hottip);
            });
            console.log(exoneration, hottip);

            const exoner = JSON.stringify(exoneration);
            sessionStorage.setItem("exonerationNoticeCardRisk", exoner);
            sessionStorage.setItem("Risk", JSON.stringify(exonerationNoticeCardRisk));

            const XPX = sessionStorage.getItem("XPX");

            exoneration.content.length > 0 && materialList.data.push(exoneration);
            hottip.content.length > 0 && materialList.data.push(hottip);
            commit("SET_Exoneration_Notice", exonerationNoticeCardRisk);
            if (XPX != '' && XPX == 200) {

            } else {
                showDialog(isShow, () => {
                    skeleton.hide();
                })
            }

            isDialog = true
            console.log(isDialog,'isDialog')
            productData.data.prdInsuranceDutyList.sort(function (a, b) {
                return a.sortNum - b.sortNum
            })
            commit("SET_PRODUCT_DATA", productData.data)
            commit("SET_MATERIAL_LIST", materialList.data)
            sessionStorage.setItem("productData",JSON.stringify(productData.data));
            sessionStorage.setItem("pdfList", JSON.stringify(materialList.data?materialList.data:[]));
            sessionStorage.setItem("Risk", JSON.stringify(exonerationNoticeCardRisk));
            if (XPX != '' && XPX == 200) {
                router.push({
                    name: "insuranceInformation",
                    params: state.routeParams,
                    query: state.routeQuery
                })
            } else {
                router.replace({
                    name: "card-info",
                    params: state.routeParams,
                    query: state.routeQuery
                })
            }
        },
        getOpenId: ({dispatch}, code, isYxpt) => {
            api.common.getOpenId({code}, true).then(res => {
                if (res.status === "1") {
                    localStorage.setItem("elife-openid", res.data.openid);
                    sessionStorage.setItem('elife-wxCode', code);
                    dispatch("getUserInfo", res.data.openid)
                }
            })
        },
        getUserInfo: ({commit}, thirtyCode) => {
            api.common.getUserInfo({thirtyCode}, true)
                .then(res => {
                    console.log(res);
                    if (res.status === "1") {
                        commit("SET_USER_INFO", {policyInfo: res.data, hasUserInfo: true})
                    }
                })
        },
        mobileCheck: ({state}, param) => {
            console.log(state);
            return api.common.mobileCheck(param, true)
        },
        checkHold: ({state, dispatch,commit}, data) => {
            commit('SET_ORDERCODE',data.orderCode)
            // state.event = TINGYUN.createEvent({
            //     name: "卡单核保",
            //     key: '82340c79-5a1c-4faa-99f1-6ff49fe923d5'
            // })
            state.holdData = data
            if (state.holdData.plchdCrdtExpDt == "长期") {
                state.holdData.plchdCrdtExpDt = "99991231"
            }
            if (state.holdData.rcgnCrdtExpDt == "长期") {
                state.holdData.rcgnCrdtExpDt = "99991231"
            }
            api.card.checkHold(data).then(res => {
                if (res.status === "1") {
                    state.time = 0;
                    dispatch("cardCheckHoldResult", res.data)
                } else {
                    skeleton.hide();
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "0",
                            message: res.error.message
                        }
                    })
                }
            })
        },
        cardCollectionImport: ({state}, data) => {
            state.holdData = data;
            api.card.cardCollectionImport(data).then(res => {
                if (res.status === "1") {
                    console.log(res.data);
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "2"
                        }
                    })
                    loading.hide();
                    // state.time = 0;
                    // dispatch("cardCheckHoldResult", res.data)
                } else {
                    skeleton.hide();
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "0",
                            message: res.error.message
                        }
                    })
                }
            })
        },
        cardCheckHoldResult: ({state, dispatch, getters}, flowNo) => {
            if (++state.time > 20) return state.event.fail();
            api.card.cardCheckHoldResult({flowNo}).then(res => {
                if (res.status === "1" && !res.data) {
                    setTimeout(() => {
                        dispatch("cardCheckHoldResult", flowNo);
                    }, 3000);
                } else if (res.status === "0") {
                    loading.hide();
                    return;
                } else if (res.status === "1" && res.data) {
                    loading.hide();
                    console.log(res);
                    if (res.data.respCode === "0") {
                        // state.event.fail();
                        console.log(res);
                        router.replace({
                            name: "card-result",
                            params: {
                                type: "0",
                                message: res.data.respMsg
                            }
                        })

                    } else {
                        // state.event.end();
                             //   如果是学平险   改失效的时间
                             let holdResult = {}
                             if (JSON.parse(sessionStorage.getItem('planPage'))) {
                                let Time = JSON.parse(sessionStorage.getItem('planPage'))
                                let startime =
                                    `${Time.time.slice(0, 4)}` + `-${Time.time.slice(4, 6)}` + `-${Time.time.slice(6, 8)}`;
                                let endtime = `${Time.endtime.slice(0, 4)}` + `-${Time.endtime.slice(4, 6)}` + `-${Time.endtime.slice(6, 8)}`;
    
                                holdResult = {
                                    resinfo: res.data.respCode,
                                    name: state.holdData.plchdNm,
                                    cardno: res.data.cardNo,
                                    priCardNo: res.data.priCardNo,
                                    prdname: getters.product.prd_name,
                                    prdInsuranceDutyList: getters.product.prdInsuranceDutyList,
                                    // 保险开始时间
                                    tipstarttime: startime,
                                    // 保险结束时间
                                    tipendtime: endtime,
                                    plan: getters.product.plans[0]
                                }
    
                            }else {
                                holdResult = {
                                    resinfo: res.data.respCode,
                                    name: state.holdData.plchdNm,
                                    cardno: res.data.cardNo,
                                    priCardNo: res.data.priCardNo,
                                    prdname: getters.product.prd_name,
                                    prdInsuranceDutyList: getters.product.prdInsuranceDutyList,
                                    // 保险开始时间
                                    tipstarttime: state.holdData.insPolcyIntndEfDt.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3"),
                                    // 保险结束时间
                                    tipendtime: res.data.insEndDate ? res.data.insEndDate.slice(0, 10) : '',
                                    plan: getters.product.plans[0]
                                }}
                        //转成json格式的数据
                        localStorage.setItem("card-holdResult", JSON.stringify({...holdResult, ...getters.product}));
                        if ((!state.routeQuery.referrerCode || !sessionStorage.getItem("referrer")) && !state.schoolData.trainAddress) {
                            dispatch("orderInfoUpdate", res.data.orderCode)
                        }
                        if (res.data.personalPay === "1") {
                            router.replace({
                                name: "card-result",
                                params: {
                                    type: 3,
                                    orderCode: res.data.orderCode
                                }
                            })
                        } else {
                            dispatch("payFormCheck", res.data.orgCode)
                        }

                    }

                }
            })
        },
        orderInfoUpdate({state}, orderCode) {
            var param = {}
            if (state.routeQuery.sendSalerId) {
                param = {
                    sceneCode: state.sceneCode || state.routeQuery.sceneCode || '001',
                    sendSalerId: state.sendSalerId || state.routeQuery.sendSalerId || '',
                    orderCode
                }
            }
            if (state.routeQuery.openId) {
                param = {
                    sceneCode: state.sceneCode || state.routeQuery.sceneCode || '002',
                    sendSalerId: state.openId || state.routeQuery.openId || '',
                    orderCode
                }
            }
            if (state.routeQuery.referrerCode || sessionStorage.getItem("referrer")) {
                param = {
                    sceneCode: state.sceneCode || state.routeQuery.sceneCode || '004',
                    sendSalerId: state.routeQuery.referrerCode || JSON.parse(sessionStorage.getItem("referrer")).referrerCode || "",
                    orderCode
                }
            }
            if (state.schoolData && state.schoolData.trainAddress) {
                param.trainAddress = state.schoolData.trainAddress || "" //培训地址
                param.studentIdentity = state.schoolData.studentIdentity || "" //学员身份
                param.trainClassName = state.schoolData.trainClassName || "" //培训班名称
                param.orderCode = orderCode
                if (!param.sceneCode) {
                    param.sceneCode = state.schoolData.sceneCode
                }
            }
            if ((Object.keys(state.routeQuery).length != 0 || sessionStorage.getItem("referrer") || state.schoolData.trainAddress) && !state.routeQuery.SaleChnl && !state.routeQuery.channel) {
                api.card.orderInfoUpdate(param, true).then(res => {
                    console.log(res)
                })
            }

        },
        saveRecord({state}, pid) {
            console.log(state.routeQuery, pid, 'state.routeQuery,pid')
            if (JSON.parse(sessionStorage.getItem("referrer")).save == '1') {
                var param = {
                    prdId: JSON.parse(sessionStorage.getItem("referrer")).prdId || "",
                    recvName: JSON.parse(sessionStorage.getItem("referrer")).referrerName || state.routeQuery.referrerName,
                    recvId: localStorage.getItem("elife-openid"),
                    sendAgentNumber: JSON.parse(sessionStorage.getItem("referrer")).referrerCode || state.routeQuery.referrerCode,
                    pid: pid || '',
                    isOpen: 1
                }
                api.card.saveRecord(param, true).then(res => {
                    console.log(res)
                })
            } else if (JSON.parse(sessionStorage.getItem("referrer")).save == '2') {
                var param = {
                    prdId: JSON.parse(sessionStorage.getItem("referrer")).prdId || "",
                    recvName: JSON.parse(sessionStorage.getItem("referrer")).referrerName || state.routeQuery.referrerName,
                    recvId: localStorage.getItem("elife-openid"),
                    sendAgentNumber: JSON.parse(sessionStorage.getItem("referrer")).referrerCode || state.routeQuery.referrerCode,
                    pid: pid || '',
                    isOpen: 0
                }
                api.card.saveRecord(param, true).then(res => {
                    console.log(res)
                })
            }
        },
        payFormCheck({state, getters}, branchCode) {
            let key = ['0207'] //支付方式为实时收付费时，将其他收银台付款方式去除
            const ua = navigator.userAgent.toLowerCase();
            let platForm;
            if (ua.indexOf("android") != -1) {
                platForm = "android";
            } else if (ua.indexOf("iphone") != -1) {
                platForm = "iPhone";
            } else {
                platForm = "pc";
            }
            if(navigator.userAgent.indexOf('CloudMercWebView') == -1){
                // 查询是否为建行生活场景，非建行生活场景，将建行生活支付方式剔除
                if (getters.product.payType) {
                    if(getters.product.payType.length >= 2){
                        let str = getters.product.payType.split(',')
                        let payTypeArray = []
                        for(let i=0; i<str.length;i++){
                            payTypeArray.push(str[i])
                        }
                        payTypeArray.forEach((item,index)=>{
                            key.find((tt)=>{
                            if(tt === item){
                                payTypeArray.splice(index,1)
                            }
                          })
                        })
                        getters.product.payType = String(payTypeArray)
                    }else{
                        key.map(item=>{
                            if(item == getters.product.payType) getters.product.payType = ''
                        })
                    }
                }
            }
            let payData = {
                payTypeCode: getters.product.payType.indexOf("05") > -1 ? "02" : "01",
                cardSaleType: getters.product.cardSaleType,
                ccbMerchantId: getters.product.ccbMerchantId,
                ccbPosId: getters.product.ccbPosId,
                backUrlType: "elife-card/#/card/card-result/3/0/" + state.holdData.orderCode,
                wxCode: sessionStorage.getItem("elife-wxCode") || "",
                customerId: state.policyInfo.cusId || "",
                branchCode,
                orderCode: state.holdData.orderCode,
                payAmount: getters.product.initPyFAmt, // 保单金额
                platForm,
                browserType: (state.holdData.channelCode === "0011" || state.holdData.channelCode === "9999") ? "01" : "02",
                opendId: localStorage.getItem("elife-openid") || "blank",
                channelCode: state.channelCodeOld,
                timestamp: new Date().getTime() + Math.random(),
                paySign: ''
            };
            var getCode = JSON.parse(localStorage.getItem("SET_IMG_CODE"))
            var BATCHNO = JSON.parse(localStorage.getItem("BATCHNO"))
            var payReqData = {
                branchCode,
                payAmount: getters.product.initPyFAmt, // 保单金额
                orderCode: state.holdData.orderCode,
                setimgsession: state.imgCode.traceId || (getCode ? getCode.traceId : ''), //视频回溯唯一标志会话id
                imgversion: state.imgCode.verNo || (getCode ? getCode.verNo : ''), //平台页面版本号
                imggoodsid: getters.product.prdId,  //保险产品id
                imggoodsname: getters.product.prd_name, //保险产品名称
                imgpagenum: '1', //页面节点编号，如第一个页面可写1
                website:state.getType || '10000004',
                batchNo:state.batchNo || BATCHNO

            };
          
            api.card.checkNewPay(payReqData).then(paywayres => {
                if (paywayres.status == '1') {
                    payData.paySign = paywayres.data.paySign
                    // payData.trackvideo = paywayres.data.payNewSign || ''
                    
                    // alert("跳转")
                    // return
                    // 如果场景为建行生活时，产品中无可用支付方式，唤起建行生活支付。
                    // 如果场景为建行生活时，支付方式有建行支付，则向建行生活推送订单信息
                    // 订单推送成功，唤起建行收银台，完成支付
                 
                    if (navigator.userAgent.indexOf('CloudMercWebView') > -1 && getters.product.payType.indexOf("0207") > -1) {
                        payData.userId = state.lifeInfo.USERID,//客户ID
                        payData.initSumAmt = getters.product.initSumAmt,//订单原金额
                        payData.initPyFAmt = state.batchInfo.initPyFAmt,//订单实际支付金额
                        payData.coPyFAmt = state.batchInfo.coPyFAmt,//第三方平台优惠金额
                        payData.discountAmtDesc = ''//第三方平台优惠定义
                        payData.channelCode = state.channelCode;
                          // 新增需要缓存的字段
                      let cache=JSON.parse(localStorage.getItem('card-holdResult'));
                      let prdInsuranceDutyList=cache.prdInsuranceDutyList.map((item)=>{
                        return{
                            ilName:item.ilName,
                            modifyTime:item.modifyTime,
                            description:item.description
                        }
                    }
                    )
                    let cacheResult={
                        name:cache.name,
                        cardno:cache.cardno,
                        prdname:cache.prdname,
                        prdInsuranceDutyList:prdInsuranceDutyList,
                        cardSaleType:cache.cardSaleType,
                        plan:cache.plan,
                        prdId:cache.prdId,
                        priCardNo:cache.priCardNo,
                        batchNo:cache.batchNo,
                        tipstarttime:cache.tipstarttime,
                        tipendtime:cache.tipendtime,
                        takeEffectFlag:cache.takeEffectFlag,
                    }
                        payData.results=JSON.stringify(state.result),//结果页面的商品信息
                        payData.batchNo=getters.product.batchNo,//批次号
                        payData.cardHoldResult=JSON.stringify(cacheResult),//前端缓存的
                        payData.plchdNm=state.holdData.plchdNm,//客户注册信息
                        payData.prdName=state.productData.prd_name,//商品信息
                            // 到这里
                        api.card.mebOrderPush(payData, true).then(res => {
                            if (res.status == '1'){
                                router.replace({
                                    name: "blank-page",
                                    params: {
                                        payData:res.data
                                    }
                                })
                            }else{
                                router.replace({
                                    name: "card-result",
                                    params: {
                                        type: "0",
                                        message: res.error.message
                                    }
                                })
                            }
                        })
                    } else {
                    // 如果场景非建行生活时，查看支付方式中是否有建行生活支付方式，如果有，从中剔除。 
                        api.card.payFormCheck(payData).then(res => {
                            if (res.status === "1") {
                                const ua = platForm === "iPhone" ? "&CCBWebView-User-Agent=CCBWebView/*/iPhone/*/iPhoneOS5.0/*/2.12/*/640*960" : "&CCBWebView-User-Agent=CCBWebView/*/Android/*/Android7.0/*/4.00/*/640*960"
                                const payRedirectUrl = getters.product.payType.indexOf("05") > -1 ? res.data.payRedirectUrl + ua : res.data.payRedirectUrl;
                                window.location.href = payRedirectUrl + "&timestamp=" + (new Date().getTime() + Math.random()) + "&trackvideo=" + paywayres.data.payNewSign;
                            } else {
                                console.log(res);
                                loading.hide();
                                skeleton.hide();
                                router.replace({
                                    name: "card-result",
                                    params: {
                                        type: "0",
                                        message: res.error.message
                                    }
                                })
                            }
                        })
                    }
                }
            })
        },
        dispatchFindPolicyNo({dispatch, state,commit}, orderCode) {
            skeleton.show();
            state.time = 0;
            commit("SET_ORDERCODE",orderCode)
            dispatch("findPolicyNo", orderCode)
        },
        findPolicyNo({state, commit, dispatch}, orderCode) {
            if (++state.time > 20) return;
            api.common.findPolicyNo({orderCode}, true).then(res => {
                if (res.status === "1" && (!res.data || res.data.length < 1)) {
                    setTimeout(() => {
                        dispatch("findPolicyNo", orderCode)
                    }, 1500)
                    const ccbPosid = sessionStorage.getItem('ccbPosid') || ''
                    if (++state.time > 8 && ccbPosid) {
                        sessionStorage.removeItem('ccbPosid')
                        let param = {
                                TX_TYPE:'0',
                                TXN_PRD_TPCD:'06',
                                ONLN_PY_TXN_ORDR_ID:orderCode,//订单号
                                POS_CODE:ccbPosid,//柜台号
                                TXN_STATUS:'02'
                            }
                        api.card.platOrderQry(param,true).then(res=>{
                            console.log(res);
                        })
                    }
                } else if (res.status === "1" && res.data) {
                    skeleton.hide();
                    commit("SAVE_RESULT", res.data.map(v => v.pbInsuSlipNo))
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "1",
                        }
                    })
                } else {
                    skeleton.hide();
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "0",
                            message: res.error.message
                        }
                    })
                }
            })
        },
        async saveYntInfo(param){
            console.log(param,'param')
            let yntInfo = JSON.parse(sessionStorage.getItem('yntInfo'))
            let data = {
                inuredStatus:param.type == '3' ? '1' : '0',
                remark:param.type == '3' ? '' : param.message,
                ...yntInfo
            }
            await api.card.cusInfoEncode({
                cid: 'JHYNT',
                bus: 'YNT001',
                ciText: data
            },true).then(res => {
                console.log(res,'裕农通信息缓存')
            })
        },
        cardValidationCretNo({ state, commit,dispatch }, params) {
            commit("SET_BATCHNO",params.batchNo)
            api.card.cardValidationCretNo(params).then(res => {
                if (res.status === "1") {
                    state.time = 0;

                    dispatch("queryCretNoValidationResult", res.data)
                } else {
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "0",
                            message: res.data.respMsg
                        }
                    })
                }
            })
        },
        queryCretNoValidationResult({state, dispatch, commit}, flowNo) {
            if (++state.time > 20) return;
            api.card.queryCretNoValidationResult({flowNo}).then(res => {
                if (res.status === "1" && !res.data) {
                    setTimeout(() => {
                        dispatch("queryCretNoValidationResult", flowNo)
                    }, 3000)
                } else if (res.status === "1" && res.data) {
                    loading.hide();
                    if (res.data.plchdRespCode == '1') {
                        state.checkPass = true;
                        const {plchdCrdtNo, plchdMoveTelNo, plchdNm, plchdEmailAdr} = res.data;
                        const policyInfo = {
                            cretNo: plchdCrdtNo,
                            phoneNo: plchdMoveTelNo,
                            customerName: plchdNm,
                            plchdEmailAdr
                        }
                        if (plchdMoveTelNo) {
                            commit("SER_CHECK_FLAG", false)
                        }
                        commit("SET_USER_INFO", {policyInfo, hasSomeUserInfo: true})
                        commit("SET_BATCH_INFO", res.data)
                        dispatch("getProductResult", { prdId: res.data.prdId })
                    };
                    if (res.data.plchdRespCode == '0') {
                        router.replace({
                            name: "card-result",
                            params: {
                                type: "0",
                                message: res.data.plchdRespDesc
                            }
                        })
                        skeleton.hide()

                    };
                } else {
                    router.replace({
                        name: "card-result",
                        params: {
                            type: "0",
                            message: res.data.message
                        }
                    })
                }

            })
        },
        shareOnWechat({state}, payLoad) {
            console.log(state);
            const {title, desc, link, imgUrl, currentUrl} = payLoad;

            api.common.Signature({currentUrl}, true).then(res => {
                if (res && res.status === "0") return;
                const wx = window.wx;
                console.log(res);
                const {appId, timeStamp, nonceStr, signature} = res.data || {};
                console.log(appId, timeStamp, nonceStr, signature);

                wx.config({
                    debug: true,
                    appId,
                    timestamp: timeStamp,
                    nonceStr,
                    signature,
                    jsApiList: ["updateAppMessageShareData", "updateTimelineShareData"]
                });
                wx.ready(function () {
                    //需在用户可能点击分享按钮前就先调用
                    wx.updateAppMessageShareData({
                        title, // 分享标题
                        desc, // 分享描述
                        link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                        imgUrl, // 分享图标
                        success: function () {
                            // 设置成功
                        }
                    });
                    //需在用户可能点击分享按钮前就先调用
                    wx.updateTimelineShareData({
                        title, // 分享标题
                        link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                        imgUrl, // 分享图标
                        success: function () {
                            // 设置成功
                        }
                    });
                });
            });
        },
        wechatShareLink({state}, param) {
            console.log(state)
            let wxShareInfo = param.wxShareInfo
            let params = {
                currentUrl: param.currentUrl
            }
            console.log(param)
            api.common.signature(params, true).then(res => {
                loading.hide();
                if (res.status == 1) {
                    const wx = window.wx;
                    wx.config({
                        debug: false,
                        appId: res.data.appId,
                        timestamp: res.data.timeStamp,
                        nonceStr: res.data.nonceStr,
                        signature: res.data.signature,
                        jsApiList: ['updateAppMessageShareData', 'updateTimelineShareData', 'onMenuShareAppMessage', 'onMenuShareTimeline']
                        // jsApiList: ['updateAppMessageShareData']
                    });
                    wx.ready(function () {
                        wx.updateAppMessageShareData({
                            title: wxShareInfo.title,
                            desc: wxShareInfo.desc,
                            link: wxShareInfo.link,
                            imgUrl: wxShareInfo.imgUrl,
                            success: function () {

                            }
                        })
                        wx.updateTimelineShareData({
                            title: wxShareInfo.title,
                            link: wxShareInfo.link,
                            imgUrl: wxShareInfo.imgUrl,
                            success: function () {

                            }
                        })
                    })
                }
            })
        },
        getsalerWXShare({state}, param) {
            api.static.salerWXShare().then(res => {
                loading.hide();
                for (let k in res.cardwxShare) {
                    if (k == param) state.wxShareInfo = res.cardwxShare[k].wxShareInfo
                }
            })
            // dispatch("orderInfoUpdate","1045454545454545")
        }
    }
}

export default app