import apis from "./config"
import { axios } from "@/utils/request"
import { install } from './installer'
let vars = {}
const BASE_URL = process.env.VUE_APP_API_BASE_URL || '';
console.log()
for (const iterator of Object.entries(apis)) {
    const key = iterator[0], value = iterator[1]
    vars[key] = {}
    for (const item of Object.entries(value)) {
        const ckey = item[0], cvalue = item[1];
        const url = cvalue.url, method = cvalue.method
        if(method.toLocaleUpperCase() === "URL") {
            vars[key][ckey] = (data,noLoading) => {
                return axios({
                    url:BASE_URL+url+data.url,
                    method:"get",
                    params:data.params,
                    noLoading
                })
            }
        }else if(method.toLocaleUpperCase() === "GET") {
            vars[key][ckey] = (data,noLoading) => {
                return axios({
                    url:BASE_URL+url,
                    method,
                    params:data,
                    noLoading
                })
            }
        }else if(method.toLocaleUpperCase() === "POST") {
            vars[key][ckey] = (data,noLoading) => {
                return axios({
                    url:BASE_URL+url,
                    method,
                    data,
                    noLoading
                })
            }
        }
    }
    // vars[iterator[0]]
}
// vars.a = (data) => {
//     return axios({
//         url: '456464',
//         method: "post",
//         data
//     })
// }
const installer = {
    vm: {},
    install(Vue) {
        Vue.use(install, vars)
    }
}
export {
    vars as api,
    installer as apiInstaller
};