// eslint-disable-next-line
// import Vue from "vue";
import axios from "axios";
import { VueAxios } from "./axios";
import loading from "@/components/loading/index.js";
import router from "@/router";
import skeleton from "@/components/skeleton";

const baseUrl =
  "" || location.origin + location.pathname.replace("/elife-card", "");

const service = axios.create({
  baseURL: process.env.NODE_ENV === "production" ? baseUrl : "",
  timeout: 30000,
});

const err = (error) => {
  loading.hide();
  skeleton.hide();
  if (error.response) {
    const data = error.response.data;
    console.log(data);
    router.replace({
      name: "card-result",
      params: {
        type: "0",
        message: "维护中，请稍后",
      },
    });
  }
  return Promise.reject(error);
};

service.interceptors.request.use((config) => {
  // console.log(config);
  if (!config.noLoading) loading.show();
  const userData = sessionStorage.getItem("elife-userData");
  const { token, cusId } = userData ? JSON.parse(userData) : {};
  // const channelCode = sessionStorage.getItem("channelCode") || '';
  const channelCode = sessionStorage.getItem("channelCodeOld") || "";
  token && (config.headers["x-token"] = token);
  cusId && (config.headers["x-cusId"] = cusId);
  cusId && (config.headers["x-custId"] = cusId);
  config.headers["x-channelCode"] = channelCode;
  config.headers["x-timestamp"] = +new Date();
  return config;
});
service.interceptors.response.use((response) => {
  const data = response.data;
  if (data.status && data.status === "0") {
    loading.hide();
  }
  return data;
}, err);

const installer = {
  vm: {},
  install(Vue) {
    Vue.use(VueAxios, service);
  },
};

export { installer as VueAxios, service as axios };
