const VueAxios = {
    vm: {},
    install(Vue, instance) {
        if (this.installed) {
            return
        }
        this.installed = true
        if (!instance) {
            console.error("axios is not find please check package.json")
            return;
        }
        Vue.axios = instance
        Object.defineProperties(Vue.prototype, {
            axios: {
                get: function get() {
                    return instance
                }
            },
            $http: {
                get: function get() {
                    return instance
                }
            }
        })
    }
}

export {
    VueAxios
}